.bulletin-container{padding:2rem;background-color:#fff9fc;border-radius:20px;box-shadow:0 4px 15px rgba(255,182,193,.2);max-width:1200px;margin:2rem auto;border:2px solid #ffb6c1}.bulletin-container h2{text-align:center;color:hotpink;margin-bottom:2rem;font-size:2.2em;font-family:"Comic Sans MS",cursive;text-shadow:2px 2px 4px rgba(255,182,193,.3)}.bulletin-container table{width:100%;border-collapse:separate;border-spacing:0 8px;margin-bottom:2rem}.bulletin-container table th{padding:1rem;text-align:left;border-bottom:3px solid #ffb6c1;background-color:#fff0f5;color:hotpink;font-weight:600;font-family:"Comic Sans MS",cursive}.bulletin-container table th:first-child{border-radius:12px 0 0 12px}.bulletin-container table th:last-child{border-radius:0 12px 12px 0}.bulletin-container table td{padding:1rem;background-color:#fff;border-bottom:2px solid #ffe4e1}.bulletin-container table td:first-child{border-radius:12px 0 0 12px;color:hotpink;font-weight:500}.bulletin-container table td:last-child{border-radius:0 12px 12px 0}.bulletin-container table td input{width:70px;padding:.5rem;text-align:center;border:2px solid #ffb6c1;border-radius:12px;font-size:1em;background-color:#fff;color:hotpink;transition:all .3s ease}.bulletin-container table td input:focus{outline:none;border-color:hotpink;box-shadow:0 0 10px rgba(255,105,180,.2)}.bulletin-container table tr{transition:transform .2s ease}.bulletin-container table tr:hover{transform:scale(1.02);background-color:#fff0f5}.bulletin-container .moyenne-generale{margin-top:2rem;padding:1.5rem;background-color:#fff0f5;border-radius:15px;text-align:center;border:2px solid #ffb6c1;position:relative}.bulletin-container .moyenne-generale::before{content:"ðŸŽ€";position:absolute;top:-15px;left:50%;transform:translateX(-50%);font-size:1.5em}.bulletin-container .moyenne-generale h3{color:hotpink;margin-bottom:1rem;font-size:1.5em;font-family:"Comic Sans MS",cursive}.bulletin-container .moyenne-generale .appreciation{font-size:1.1em;color:hotpink;font-weight:500;font-family:"Comic Sans MS",cursive}