.double-section{padding:20px;background-color:#ffe6ee;border-radius:15px;margin:20px 0;display:flex;flex-direction:column;align-items:center}.double-section h2{color:hotpink;margin-bottom:20px;text-align:center}.double-section .exercise-row{display:flex;align-items:center;gap:10px;margin-bottom:15px;font-size:24px;background-color:#ff0080;padding:15px;border-radius:10px;box-shadow:0 2px 5px rgba(0,0,0,.1);color:#000;width:80%;font-weight:bold}.double-section .exercise-row input{width:80px;height:40px;text-align:center;font-size:24px;border:3px solid #000;border-radius:8px;color:hotpink}.double-section .exercise-row input::placeholder{color:#ffb6c1;font-size:20px}.double-section .exercise-row .feedback{margin-left:15px;font-size:20px;animation:bounce .5s;display:flex;align-items:center;white-space:nowrap;min-width:200px}.double-section .exercise-row .feedback:not(.correct){color:#f44;background:#fff;padding:8px 15px;border-radius:20px;border:2px solid #f44}.double-section .exercise-row .feedback.correct{color:hotpink;font-family:"Comic Sans MS",cursive;display:flex;align-items:center;gap:8px;padding:8px 15px;background:#fff;border-radius:20px;border:2px solid #ffb6c1;box-shadow:0 2px 10px rgba(255,105,180,.2)}.double-section .exercise-row .feedback.correct::before{content:"âœ¨"}.double-section .exercise-row .feedback.correct::after{content:"âœ¨"}@keyframes bounce{0%{transform:scale(0.3)}50%{transform:scale(1.2)}70%{transform:scale(0.9)}100%{transform:scale(1)}}@keyframes sparkle{0%{opacity:.2}50%{opacity:1}100%{opacity:.2}}